import React, { useState } from "react";

function Carousel({ arr }) {
  const [index, setIndex] = useState(0);
  const isPrev = (i) => {
    if (index === 0) {
      if (i === arr.length - 1) return true;
    } else {
      if (i === index - 1) return true;
    }
    return false;
  };
  const isNext = (i) => {
    if (index === arr.length - 1) {
      if (i === 0) return true;
    } else {
      if (i === index + 1) return true;
    }
    return false;
  };
  return (
    <div className="tab-content">
      {arr.map((image, i) => {
        return (
          <img
            src={image}
            className={`item ${
              index === i
                ? "active"
                : isPrev(i)
                ? "prev"
                : isNext(i)
                ? "next"
                : "inactive"
            }`}
            key={i}
            alt=""
            onClick={() => setIndex(i)}
          />
        );
      })}
    </div>
  );
}

export default Carousel;
